import React from 'react';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile/GridListTile';

const imagesPath = process.env.REACT_APP_PHOTO_URL as string;
const thumbnailsPath = `${imagesPath}/thumbnails`;

const defaultCols = 4;
const maxCols = 2;
let setCols = defaultCols;

const getCols = (): number => {
  if (setCols < 1) {
    setCols = defaultCols;
  }
  const tempCols = Math.floor(Math.random() * maxCols + 1);
  if (tempCols > setCols) {
    const temp = setCols;
    setCols = defaultCols;
    return temp;
  }
  setCols -= tempCols;
  return tempCols;
};

const images = [...new Array(23)].map((item, index) => ({
  original: `${imagesPath}/img${index + 1}.jpeg`,
  thumbnail: `${thumbnailsPath}/img${index + 1}.jpeg`,
  cols: getCols(),
  title: 'img',
}));

export const Gallery = () => {
  const gridImages = images.map((image) => (
    <GridListTile key={image.thumbnail} cols={image.cols || 1}>
      <img src={image.thumbnail} alt={image.title} />
    </GridListTile>
  ));

  return (
    <div>
      <GridList cellHeight={250} cols={defaultCols}>
        {gridImages}
      </GridList>
    </div>
  );
};
