import React, { useState, ChangeEvent, useEffect } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import PhoneIcon from '@material-ui/icons/Phone';
import Box from '@material-ui/core/Box';
import { useHistory, useLocation } from 'react-router-dom';
import { Logo } from '../../Logo/Logo';
import Toolbar from '@material-ui/core/Toolbar';

const useStyles = makeStyles({
  appBar: {
    backgroundColor: 'white',
    color: '#333',
  },
  toolbar: {
    alignItems: 'center',
    justifyContent: 'space-around',
  },
  logo: {
    height: '60px',
    paddingTop: '2px',
    paddingBottom: '2px',
    cursor: 'pointer',
  },
  tab: {
    minWidth: 45,
    '&:hover': {
      color: '#f50057', //'#ad2503',
    },
  },
});

type menuItem = {
  title: string;
  pageURL: string;
};

const menuItems: menuItem[] = [
  { title: 'Home', pageURL: '/' },
  { title: 'Windows', pageURL: '/windows' },
  { title: 'Doors', pageURL: '/doors' },
  { title: 'Materials', pageURL: '/materials' },
  { title: 'Service', pageURL: '/service' },
  { title: 'Gallery', pageURL: '/gallery' },
  { title: 'About Us', pageURL: '/about-us' },
  { title: 'Contact', pageURL: '/contact' },
];

export const Header = () => {
  const { pathname } = useLocation<{ pathname: string }>();
  const history = useHistory();
  const classes = useStyles();

  const [active, setActive] = useState<number | boolean>(0);

  const clickHandler = (path: string): void => {
    history.push(path);
  };

  useEffect(() => {
    const currentURL = '/' + pathname.split('/')[1];
    const indexActive = menuItems.findIndex(
      (menuItem) => menuItem.pageURL === currentURL
    );
    setActive(indexActive < 0 ? false : indexActive);
  }, [pathname]);

  const navTabs = menuItems.map((item) => (
    <Tab
      label={item.title}
      className={classes.tab}
      onClick={() => clickHandler(item.pageURL)}
    />
  ));

  return (
    <AppBar className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        <Box
          className={classes.logo}
          onClick={() => {
            history.push('/');
          }}
        >
          <Logo height />
        </Box>
        <Tabs value={active}>{navTabs}</Tabs>
        <Button startIcon={<PhoneIcon />} variant="outlined">
          +44 111 222 333
        </Button>
      </Toolbar>
    </AppBar>
  );
};
